.header {
  display: flex;
  flex: 1;
  margin: 0 0.5rem;
}

.subheader {
  font-weight: 500;
}

.game-container {
  display: flex;
  flex-direction: row;
}

.content-container {
  flex: 1;
  justify-content: flex-end;
}

.button-container {
  float: right;
}

.caret.hidden {
  opacity: 0;
  transition: all 1ms ease-in;
}

.results {
  color: rgb(227, 237, 255);
}

.results_container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.results_section {
  display: flex;
  flex: 1;
  justify-content: space-evenly;
  align-items: center;
}

.results_sub_section {
  display: flex;
  flex: 1;
  padding: 0 1rem;
}

ul {
  padding: 0;
}

#results_list li {
  margin: 0.25rem 0;
  padding: 0.6rem 1.25rem;
  display: flex;
  justify-content: space-between;
  border-radius: 4px;
}

.hidden {
  height: 0px;
  opacity: 0;
}

.shown {
  opacity: 1;
  transition: all 200ms ease-in-out;
}
